import Vue from "vue";
import VueRouter from "vue-router";
import List from "../views/List.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "List",
    component: List,
  },
  {
    path: "/task/:id",
    name: "task",
    component: () => import("@/views/Task.vue"),
  },
  {
    path: "/group/:name",
    name: "group",
    component: () => import("@/views/List.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
